import Head from 'next/head';
import Image from 'next/image';

export default function Review() {
  const team = [
    {
      name: 'John Almeda',
      role: 'CEO Company',
      reviewtitle:'Great service & great bikes!',
      reviewdescription:'I hired an e-bike for the weekend and ended up buying one! The team was super helpful, and the bike handled Melbourne’s hills like a dream.',
      img: '/images/Student-one-review.avif',
      alt: 'John Almeda profile picture',
      rating: 5,
    },
    {
      name: 'Frank Kinney',
      role: 'Financial Director',
      reviewtitle:'Eco-friendly and fun!',
      reviewdescription:'I ditched my car for an e-bike and I’m never going back. It’s affordable, eco-conscious, and actually fun to ride around Melbourne’s laneways and bike trails.',
      img: '/images/study-abroad-two.jpg',
      alt: 'Frank Kinney profile picture',
      rating: 4,
    },
    {
      name: 'Stella Larson',
      role: 'Secretary',
      reviewtitle:'Perfect for city living.',
      reviewdescription:'The e-bike is a game-changer. Charging is easy, and I can park just about anywhere. Perfect for dodging traffic and exploring Melbourne’s hidden gems.',
      img: '/images/Student-one-review.avif',
      alt: 'Stella Larson profile picture',
      rating: 5,
    },
    {
      name: 'Paul Smith',
      role: 'Sales Manager',
      reviewtitle:'Reliable, fast, and convenient.',
      reviewdescription:'Riding to uni or the markets is so much quicker now. I save money on petrol and the support team was excellent during setup and delivery.',
      img: '/images/study-abroad-two.jpg',
      alt: 'Paul Smith profile picture',
      rating: 4,
    },
  ];

  const renderStars = (count, number) => {
    return '★'.repeat(count) + '☆'.repeat(5 - count);
  };

  return (
    <>
      <Head>
        <title>Our Team</title>
        <meta
          name="description"
          content="Meet our professional and dedicated team members. Learn more about our leadership and support team."
        />
      </Head>

      <main className="container py-5">
        <section className="row g-4">
          {team.map((member, index) => (
            <article className="col-md-6 col-lg-6" key={index}>
              <div
                className="p-4 text-white text-center h-100"
                style={{ backgroundColor: '#f8f8f8', borderRadius: '1rem' }}
              >
                <div
                  className="mx-auto mb-3"
                  style={{
                    width: '100px',
                    height: '100px',
                    border: '4px solid #f7931e',
                    borderRadius: '50%',
                    overflow: 'hidden',
                    position: 'relative',
                  }}
                >
                  <Image
                    src={member.img}
                    alt={member.alt}
                    fill
                    style={{ objectFit: 'cover' }}
                    sizes="(max-width: 768px) 100px, 100px"
                  />
                </div>
                <p className="small mb-2 fw-600" style={{color:'#f7931e'}}>
                  {member.reviewtitle}
                </p>
                <p className="small mb-2 fw-600" style={{color:'#f7931e',fontStyle:'italic'}}>
                  {member.reviewdescription}
                </p>
                <div style={{ color: '#f7931e', fontSize: '1.2rem' }}>
                  {renderStars(member.rating)}
                </div>
                <div className="fw-bold" style={{color:'#f7931e',fontStyle:'italic'}}>- {member.name}</div>
                <div className="small" style={{color:'#f7931e'}}>{member.role}</div>
              </div>
            </article>
          ))}
        </section>
      </main>
    </>
  );
}
